import CustomController from "components/UI/common/Controller";
import NormalMultiSelect from "components/UI/common/NormalMultiSelect";
import { useEffect } from "react";

const OccupancyCell = ({ field, control, fieldName, setValue, watch, sequence, isCommonfield }) => {
    const occupancy = watch(`${fieldName}.occupancy`);
    const numberOfBeds = watch(`${fieldName}.numberOfBeds`);
    const beds = watch(`${fieldName}.beds`);

    useEffect(() => {
        if (occupancy === "FULL_ROOM") {
            setValue(`${fieldName}.numberOfBeds`, "N/A")
            setValue(`${fieldName}.beds`, [{
                bedNumber: "-",
                unitSequence: ""
            }])
            setValue(`${fieldName}.unitSequence`, `${sequence}-0`)
        }
        if (occupancy === "") {
            setValue(`${fieldName}.numberOfBeds`, "")
            setValue(`${fieldName}.beds`, [{
                bedNumber: "",
                unitSequence: ""
            }])
            setValue(`${fieldName}.unitSequence`, "")
        }
        if (occupancy === "SHARING") {
            if (numberOfBeds === "N/A") setValue(`${fieldName}.numberOfBeds`, "");
            else {
                if (numberOfBeds > 0 && beds.length != numberOfBeds) {
                    setValue(`${fieldName}.beds`, [{
                        bedNumber: "",
                        unitSequence: ""
                    }])
                }
            }
            setValue(`${fieldName}.unitSequence`, "")
        }
    }, [occupancy])

    return (
        <CustomController
            name={`${fieldName}.occupancy`}
            control={control}
            // error={errors?.unitDetails?.[index]?.occupancy}
            rules={{ required: true }}
            messages={{ required: "Occupancy is required" }}
            render={({ field: { onChange, value }, ...fields }) => {
                return (
                    <NormalMultiSelect
                        {...fields}
                        placeholder={"Select"}
                        customStyle1
                        menuPosition={isCommonfield ? "absolute" : "fixed"}
                        options={[
                            {
                                label: "Full Room",
                                value: "FULL_ROOM"
                            },
                            {
                                label: "Sharing",
                                value: "SHARING"
                            }
                        ]}
                        value={watch(`${fieldName}.occupancy`)}
                        name={`${fieldName}.occupancy`}
                        isMulti={false}
                        handleChange={(e, { value, name } = {}) => {
                            onChange(value);
                        }}
                    />
                );
            }}
        />
    );
};
export default OccupancyCell;
