import React from "react";
import styles from "../../../../index.module.scss";

const BedNumberTable = ({ field, fieldName, register, watch, control, isLast, showBedColumn, isSelected, isFullRoom, unitSequence, isCommonfield }) => {

    return (
        <tr style={{ position: "relative" }}>
            <td style={{ width: "146.5px", height: "45px", verticalAlign: "baseline" }} className={`${!showBedColumn ? `${styles.bg_color_accent} border-0` : ""} ${isLast ? "border-bottom-0" : ""} ${isSelected ? styles.bg_color_purple : ""}`}>
                {showBedColumn && <input
                    className={`${styles.tableInput} ${isSelected ? styles.bg_color_purple : ""}`}
                    value={field.bedNumber}
                    type="text"
                    readOnly
                />}
            </td>
            {!isCommonfield && <>
                <td style={{ height: "50px", verticalAlign: "baseline" }} className={`${styles.bg_color_accent} ${isLast ? "border-bottom-0" : ""} ${isSelected ? styles.bg_color_purple : ""}`}>
                    <input
                        className={`${styles.tableInput} ${styles.bg_color_accent} ${isSelected ? styles.bg_color_purple : ""}`}
                        value={field.unitSequence}
                        readOnly
                    />
                </td>
                {isFullRoom && <div className={styles.unit_sequence} style={{ left: showBedColumn ? 146.5 : 0 }}>
                    {unitSequence}
                </div>}
            </>}
        </tr>

    );
};
export default BedNumberTable;
