import React from "react";
import NumberOfBedsCell from "./NumberOfBedsCell";
import BedNumberTable from "./BedNumberTable";
import { useFieldArray } from "react-hook-form";

const NumberOfBedsTable = ({ field, register, fieldName, watch, setValue, control, isLast, showBedColumn, isSelected, sequence, isCommonfield }) => {

    const { fields } = useFieldArray({ control, name: `${fieldName}.beds` });

    const unitSequence = watch(`${fieldName}.unitSequence`);
    const occupancy = watch(`${fieldName}.occupancy`);

    return (
        <tr>
            <NumberOfBedsCell register={register} field={field} fieldName={fieldName} setValue={setValue} watch={watch} isLast={isLast} isSelected={isSelected} sequence={sequence} fields={fields} />
            {<td colSpan={isCommonfield ? 1 : 2} className={isLast ? "border-bottom-0" : ""}>
                {fields.map((field, i) => (
                    <BedNumberTable register={register} fieldName={`${fieldName}.beds.${i}`} field={field} watch={watch} isLast={fields.length - 1 === i} showBedColumn={showBedColumn} isSelected={isSelected} isFullRoom={occupancy === "FULL_ROOM"} unitSequence={unitSequence} isCommonfield={isCommonfield} />
                ))}
            </td>}
        </tr>
    );
};
export default NumberOfBedsTable;
