import React from "react";
import OccupancyCell from "./OccupancyCell";
import RoomNumberCell from "./RoomNumberCell";
import NumberOfBedsTable from "./NumberOfBedsTable";
import styles from "../../index.module.scss";

const RoomsRow = ({ field, fieldName, register, watch, control, index, setValue, isLast, showBedColumn, isSelected, sequence, isCommonfield }) => {

    return (
        <tr>
            <td style={{ width: "146.5px", verticalAlign: "baseline" }} className={`${isSelected ? styles.bg_color_purple : ""} ${isLast ? "border-bottom-0" : ""}`}>
                <RoomNumberCell field={field} fieldName={`${fieldName}.${index}`} register={register} watch={watch} isSelected={isSelected} />
            </td>
            <td style={{ width: "147px", verticalAlign: "baseline" }} className={`${isSelected ? styles.bg_color_purple : ""} ${isLast ? "border-bottom-0" : ""}`}>
                <OccupancyCell control={control} field={field} fieldName={`${fieldName}.${index}`} register={register} setValue={setValue} watch={watch} isSelected={isSelected} sequence={`${sequence}-${field.roomNumber}`} isCommonfield={isCommonfield} />
            </td>
            <td colSpan={isCommonfield ? 2 : 3} className={`${isSelected ? styles.bg_color_purple : ""} ${isLast ? "border-bottom-0" : ""}`}>
                <NumberOfBedsTable register={register} field={field} fieldName={`${fieldName}.${index}`} setValue={setValue} watch={watch} control={control} isLast={isLast} showBedColumn={showBedColumn} isSelected={isSelected} sequence={`${sequence}-${field.roomNumber}`} isCommonfield={isCommonfield} />
            </td>
        </tr>
    );
};
export default RoomsRow;
