import InputBox from "components/UI/common/InputBox/InputBox";
import { generateSequence } from "Constants/helperFunctions";
import { useEffect } from "react";
import styles from "../../../../index.module.scss";

const NumberOfBedsCell = ({ field, register, fieldName, watch, setValue, isSelected, sequence, fields }) => {
    const numberOfBeds = watch(`${fieldName}.numberOfBeds`);
    const bedNumberType = watch("bedNumberType");
    const sequenceFormat = watch("sequenceFormat");

    const getBedNumber = () => {
        if (sequenceFormat && bedNumberType) {
            const resp = generateSequence(bedNumberType, sequenceFormat, +numberOfBeds - 1);
            if (resp?.inBetweenSequence?.length) return resp?.inBetweenSequence;
        }
        return [];
    }

    useEffect(() => {
        console.log("HIT in NumberOfBedsCell", numberOfBeds)
        if (numberOfBeds != "N/A" && numberOfBeds > 0) {
            console.log("HIT in NumberOfBedsCell IFFFF", numberOfBeds)

            const bedNumber = getBedNumber();

            const tempArray = Array.from({ length: numberOfBeds }, (_, i) => ({
                "bedNumber": bedNumber[i],
                "unitSequence": `${sequence}-${bedNumber[i]}`
            }))
            setValue(`${fieldName}.beds`, tempArray)
        } else {
            console.log("HIT in NumberOfBedsCell ELSE", numberOfBeds)

            setValue(`${fieldName}.beds`, [{
                "bedNumber": numberOfBeds != "N/A" ? "" : "-",
                "unitSequence": numberOfBeds != "N/A" ? "" : ""
            }])
        }
    }, [numberOfBeds])

    return (
        <td style={{ width: "146.5px", verticalAlign: "baseline" }} className={`border-bottom-0 ${isSelected ? styles.bg_color_purple : ""}`}>
            <InputBox
                type={watch(`${fieldName}.occupancy`) !== "SHARING" ? "text" : "number"}
                style={{ border: "none", outline: "none", padding: "14px 21px" }}
                name={`${fieldName}.numberOfBeds`}
                className={isSelected ? styles.bg_color_purple : ""}
                placeholder={"Enter number"}
                readOnly={watch(`${fieldName}.occupancy`) !== "SHARING"}
                register={register(`${fieldName}.numberOfBeds`, {
                    pattern: /^(?!\s*$).+/,
                    validate: (value, field) => {
                        if (field.isDraft) return true;
                        if (value) return true;
                        return false;
                    },
                })}
            />
        </td>
    );
};
export default NumberOfBedsCell;
