import InputBox from "components/UI/common/InputBox/InputBox";
import { generateSequence } from "Constants/helperFunctions";
import React, { useEffect } from "react";
import { useFieldArray } from "react-hook-form";
import CheckBox from "../../../../UI/common/CheckBox/CommonCheckBox";
import styles from "../index.module.scss";
import RoomsRow from "./RoomsRow";

const FlatRow = ({
    key,
    fieldName,
    register,
    watch,
    setValue,
    control,
    setSelectedFlats = () => { },
    showExtendedColumns,
    showBedColumn,
    selectedFlats = [],
    isCommonfield
}) => {
    const numberOfRooms = watch(`${fieldName}.numberOfRooms`);
    const rooms = watch(`${fieldName}.rooms`);
    const roomNumberType = watch("roomNumberType");
    const roomFormat = watch("roomFormat");
    const flatName = watch(`${fieldName}.flatName`);
    const { fields, append, remove } = useFieldArray({ control, name: `${fieldName}.rooms` });

    const getRoomNumber = () => {
        if (roomFormat && roomNumberType) {
            const resp = generateSequence(roomNumberType, roomFormat, numberOfRooms);
            if (resp?.inBetweenSequence?.length) return resp?.inBetweenSequence;
        }
        return [];
    }

    useEffect(() => {
        if (numberOfRooms > 0) {
            if (rooms?.length > 0 && numberOfRooms > 0) {
                if (rooms?.length != numberOfRooms) {

                    const diff = numberOfRooms - rooms?.length;

                    if (diff > 0) {
                        const roomNumber = getRoomNumber();
                        const newArray = Array.from({ length: diff }, (_, i) => ({
                            "roomNumber": roomNumber[i + rooms?.length],
                            "occupancy": "",
                            "numberOfBeds": "",
                            beds: [
                                {
                                    "bedNumber": "",
                                    "unitSequence": ""
                                }
                            ],
                            "unitSequence": ""
                        }))
                        append(newArray);
                    }
                    if (diff < 0) {
                        for (let i = Math.abs(diff); i >= 0; i--) {
                            remove(+numberOfRooms + i)
                        }
                    }
                }
            } else {
                const roomNumber = getRoomNumber();
                const tempArray = Array.from({ length: numberOfRooms }, (_, i) => ({
                    "roomNumber": roomNumber[i],
                    "occupancy": "",
                    "numberOfBeds": "",
                    beds: [
                        {
                            "bedNumber": "",
                            "unitSequence": ""
                        }
                    ],
                    "unitSequence": ""
                }))
                setValue(`${fieldName}.rooms`, tempArray)
            }
        } else {
            setValue(`${fieldName}.rooms`, [])
        }
    }, [numberOfRooms])

    console.log(fieldName)
    return (
        <tr key={key}>
            <td style={{ padding: "0px 21px", verticalAlign: "baseline" }} className={selectedFlats.includes(flatName) ? styles.bg_color_purple : ""}>
                <div className="d-flex align-items-center">
                    {isCommonfield ? <>
                        <div className="d-flex align-items-center">
                            <InputBox
                                className={`${styles.font} px-2 d-flex`}
                                readOnly
                                value={roomFormat}
                            />
                        </div>
                    </> :
                        <>
                            <div className="d-flex ">
                                <CheckBox
                                    onChange={(e) => {
                                        if (flatName) {
                                            if (e.target.checked) setSelectedFlats(prev => [...prev, flatName ?? false].filter(Boolean));
                                            else setSelectedFlats(prev => prev.filter(f => f !== flatName));
                                        }
                                    }}
                                />
                            </div>
                            <div className="d-flex align-items-center">
                                <InputBox
                                    className={`${styles.font} px-4 d-flex ${selectedFlats.includes(flatName) ? styles.bg_color_purple : ""}`}
                                    name={`${fieldName}.flatName`}
                                    readOnly
                                    register={register(`${fieldName}.flatName`)}
                                />
                            </div>
                        </>}
                </div>
            </td>

            {/* Number Of Rooms */}
            <td style={{ verticalAlign: "baseline" }} className={`${selectedFlats.includes(flatName) ? styles.bg_color_purple : ""}`}>
                <InputBox
                    type="number"
                    style={{ border: "none", outline: "none" }}
                    name={`${fieldName}.numberOfRooms`}
                    placeholder={"Enter number"}
                    className={`${selectedFlats.includes(flatName) ? styles.bg_color_purple : ""}`}
                    register={register(`${fieldName}.numberOfRooms`, {
                        pattern: /^(?!\s*$).+/,
                        validate: (value, field) => {
                            if (field.isDraft) return true;
                            if (value) return true;
                            return false;
                        },
                    })}
                />
            </td>

            {numberOfRooms > 0 ? <>
                <td colSpan={isCommonfield ? 4 : 5}>
                    {fields.map((field, index) => (
                        <RoomsRow
                            field={field}
                            fieldName={`${fieldName}.rooms`}
                            register={register}
                            watch={watch}
                            control={control}
                            index={index}
                            setValue={setValue}
                            isLast={numberOfRooms - 1 === index}
                            showBedColumn={showBedColumn}
                            isSelected={selectedFlats?.includes?.(flatName)}
                            sequence={flatName}
                            isCommonfield={isCommonfield}
                        />
                    ))}
                </td>
            </> : <>
                {showExtendedColumns && <>
                    <td className={styles.bg_color_gray}></td>
                    <td className={styles.bg_color_gray}></td>
                    <td className={styles.bg_color_gray}></td>
                    {showBedColumn && <td className={styles.bg_color_gray}></td>}
                </>}
                {!isCommonfield && <td className={styles.bg_color_accent}></td>}
            </>}
        </tr>
    );
};
export default FlatRow;
